<template>
  <div>
    <van-nav-bar title="请输入支付密码" left-arrow @click-left="res" />
    <div class="prive">￥{{$store.state.myjp.total}}</div>

    <van-nav-bar left-text="支付方式" right-text="零钱>" class="navBar"></van-nav-bar>
    <van-password-input :value="value" :focused="showKeyboard" @focus="showKeyboard = true" />
    <!-- 数字键盘 -->
    <van-number-keyboard
      :show="showKeyboard"
      @input="onInput"
      @delete="onDelete"
      @blur="showKeyboard = false"
    />
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      value: "",
      showKeyboard: true
    };
  },
  methods: {
    res() {
      // console.log('xxx');
      // console.log(this.$store.state.myjp.allOrders);
      this.$store.state.myjp.allOrders.forEach(item => {
        if (!item.type) {
          item.type = 1;
        }
      });
      this.$router.push("/allOrders");
    },
    onInput(key) {
      this.value = (this.value + key).slice(0, 6);

      if (this.value.length == 6) {
        if (this.value == 666666) {
          if (this.$store.state.balance > this.$store.state.myjp.total) {
            Toast("支付成功");
            this.$store.state.balance -= this.$store.state.myjp.total
            this.$store.state.myjp.allOrders.forEach(item => {
              if (!item.type) {
                item.type = 2;
              }
            });
            setTimeout(() => {
              this.$router.push("/allOrders");
            }, 1000);
          } else {
            // console.log(this.$store.state.myjp.total,this.$store.balance);
            Toast("支付失败，余额不足");
            this.$store.state.myjp.allOrders.forEach(item => {
              if (!item.type) {
                item.type = 1;
              }
            });
            setTimeout(() => {
              this.$router.push("/allOrders");
            }, 2000);
          }
        } else {
          Toast("密码错误，请重新输入");
          setTimeout(() => {
            this.value = "";
          }, 2000);
        }
      }
    },
    onDelete() {
      this.value = this.value.slice(0, this.value.length - 1);
    }
  },
  created() {
    this.$store.state.bool = false;
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    // this.$store.state.bool = true;
  }
};
</script>

<style lang="scss" scoped>
.van-nav-bar__text {
  color: #000;
}
.prive {
  font-size: 20px;
  text-align: center;
  font-weight: bold;
}
.navBar {
  margin: 15px 0;
  padding: 0 20px;
}
.van-hairline--left {
  border: 1px solid #ccc;
}
</style>